* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Helvetica Neue", sans-serif;
  color: #333;
  text-rendering: optimizeLegibility;
  line-height: 1.4;
}

a {
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #222;
}

p,
p * {
  font-family: "baskerville", serif;
}

.number {
  font-family: "courier", monospace;
}

span.rep {
  /* text-decoration: 3px solid underline red; */
  background-color: rgba(255, 0, 0, 0.1333333333);
  color: #d00;
  padding: 0px 8px;
}

span.dem {
  /* text-decoration: 3px solid underline blue; */
  background-color: rgba(0, 0, 255, 0.1333333333);
  color: blue;
  padding: 0px 8px;
}

span.tossup {
  background-color: #ddd;
  padding: 0px 8px;
  color: #444;
}

p.has-data {
  line-height: 1.5;
}

p.has-data span:not(.container) {
  font-family: "courier", monospace;
}